import Swiper from "swiper";
export default {
  name: "Details",
  data() {
    return {
      data_lis: {}
      , baseurl: null
      , show: false
      , radio: null
      , number: 0
      , id: null
      , goods_type: {
        parameter_id: { id: null, parameter_value: null }
      }
      , goods_data: { sale_const: null  ,stock :null}
      , img_url: ''
    };
  },
  created() {
    var that = this;
    that.get_data();
    that.baseurl = that.axios.defaults.baseURL;
  },
  mounted() {
  },
  updated() {
    this.lunbo_fun();

  }
  , methods: {
    lunbo_fun() {
      var mySwiper = new Swiper(".swiper-container", {
        pagination: ".swiper-pagination",
        loop: true,
        autoplay: {
          stopOnLastSlide: true
        },
        speed: 500,
      });
    }
    ,add_cart(){
      var that = this ;
      if(that.data_lis.product_pic.length != 0){
        if(that.goods_type.pic_id==null){
          that.$message.error('请选择参数');
          return;
        }
        
      }
      if(that.data_lis.product_parameters.length != 0){
        if(that.goods_type.parameter_id.parameter_value==null){
          that.$message.error('请选择参数');
          return;
        }
      }
      if(that.data_lis.product_plus_price.length != 0){
        if(that.goods_type.plus_price_id==null){
          that.$message.error('请选择参数');
          return;
        }
      }
      if(that.number==0){
        that.$message.error('购买数量不能为零');
        return;
      }
      let data = {data:{token:localStorage.getItem('token'),number:that.number,goods_type:that.goods_type,id:that.$route.query.id}}
      that.axios.post('user/tack_goods/updates',data).then(res=>{
        if(res.data.status){
          that.$router.push({path:'/home/tihuo'})
        }
      })
    }
    , get_data() {
      var that = this;
      let data = { data: { token: localStorage.getItem('token') } };
      that.axios.post('product/show/' + that.$route.query.id, data).then(res => {
        that.data_lis = res.data.data;
        that.goods_type.parameter_id.id = res.data.data.product_parameters.id;
        that.id = res.data.data.product_parameters.id;
        that.goods_data.sale_const = res.data.data.sale_const * 1;
        that.goods_data.stock = res.data.data.stock;
        that.img_url = that.baseurl + res.data.data.pic;
      })
    }
    , isshow() {
      var that = this;
      this.show = !this.show;
      that.goods_data.stock = that.data_lis.stock;
      that.goods_data.sale_const = that.data_lis.sale_const * 1;
      that.goods_type = { parameter_id: { id: that.id, parameter_value: null } };
      that.img_url = that.baseurl + that.data_lis.pic;
    }
    , handleChange() { }
    , chang_goods_type(type) {
      var that = this;
      if (type == 3) {
        let num = 0;
        that.data_lis.product_plus_price.forEach(e => {
          if (that.goods_type.plus_price_id == e.id) {
            num = e.values * 1;
            that.goods_data.sale_const = that.data_lis.sale_const * 1 + num;
          }
        });
      }
      if(type == 2){
        that.data_lis.product_pic.forEach(e => {
          if (that.goods_type.pic_id == e.id) {
            that.img_url = that.baseurl + e.pic;
          }
        });
      }
      that.my_post();
    }
    , my_post() {
      var that = this;
      let data = { data: { token: localStorage.getItem('token'), goods_type: that.goods_type } };
      that.axios.post('/product/stocks/' + that.$route.query.id, data).then(res => {
        that.goods_data.stock = res.data.data;
      })
    }
  }
};


