<template>
  <div class="box">
    <div class="fenxiang">
      <div v-if="kalman_pic" class="top" :style="'background: url('+kalman_pic+') no-repeat;'">
        <div class="user_box">
          <img :src="user_data.avater" alt>
          <div>
            <p>{{user_data.name}}</p>
            <!-- <p>他是普通会员</p> -->
          </div>
        </div>
        <div class="shenqing"><p>{{distribution_price}}元</p>
          <p>申请成为合伙人</p>
        </div>
        
      </div>
      <div class="vip_quanli" v-html="cont"></div>
      <el-button
        @click="add_vip(user_data.id)"
        type="primary"
        style="display:block;width:85%;margin:28px auto 68px"
        :disabled="iszhifu"
      >申请成为合伙人</el-button>
    </div>
  </div>
</template>


<script>
import citycom from "@/components/city/city";
import http from "../../api/http";

export default {
  name: "Add_vip",
  data() {
    return {
      user_data: {},
      distribution_price: null,
      kalman_pic: null,
      cont: null,
      iszhifu: false
    };
  },
  created() {
    this.get_user();
  },
  mounted() {},
  components: {
    citycom
  },
  methods: {
    get_user() {
      var that = this;
      let data = {
        data: { token: localStorage.getItem("token"), id: that.$route.query.id }
      };
      that.axios.post("/user/get_recommend", data).then(res => {
        if (res.data.status) {
          that.user_data = res.data.data.user;
          that.cont = res.data.data.fuwenben;
          that.distribution_price = res.data.data.distribution_price;
          that.kalman_pic =
            that.axios.defaults.baseURL + res.data.data.kalman_pic;
        } else {
          that.$router.push({ path: "/vip" });
        }
      });
    },
    add_vip(id) {
      var that = this;
      that.iszhifu = 1;
      setTimeout(function() {
        that.iszhifu = false;
      }, 4000);
      let data = {
        data: {
          token: localStorage.getItem("token"),
          id
        }
      };
      setTimeout(function() {
        that.iszhifu = 0;
      }, 4000);
      //请求后台接口获取数据 准备进行微信支付 
      that.axios.post("user/recommend", data).then(res => {
        //成功状态下  
        if (res.data.status) {
          // 存储微信支付数据data
          let data = res.data.data;
          console.log("即将跳转微信支付");
          //函数为分装过得  (就是调用微信支付)
          http.wexinPay(
            {
              appId: data.appId,
              nonceStr: data.nonceStr,
              package: data.package,
              paySign: data.paySign,
              signType: data.signType,
              timeStamp: data.timeStamp
            },
            //成功回调函数
            res => {
              that.$router.push({ path: "/vip" });
            }
          );
        } 
        else {
          that.$message.error(res.data.message);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;

  .vip_quanli {
    margin: 18px auto 0;
    width: 100%;
    border-radius: 18px;
  }

  .top {
    margin: 18px auto 0;
    height: 188px;
    width: 96%;
    border-radius: 18px;
    // background: url("../../../static/images/bg.png") no-repeat;
    background-size: 100% 100% !important;
    position: relative;
    .shenqing {
      position: absolute;
      bottom: 26px;
      left: 0;
      text-align: center;
      color: #fff;
      width: 100%;
      font-size: 20px;
      font-weight: 700;
    }
    .user_box {
      position: relative;
      height: 100%;
      width: 100%;
      img {
        width: 88px;
        height: 88px;
        border-radius: 50%;
        position: absolute;
        top: 28px;
        left: 28px;
      }
      div {
        position: absolute;
        top: 40px;
        left: 128px;
        font-size: 16px;
        font-weight: 700;
        color: #fff;
        p {
          margin-top: 12px;
        }
      }
      .vip_boxs {
        position: absolute;
        bottom: 8px;
        text-align: center;
        width: 100%;
        font-size: 16px;
        color: #fff;
        span:first-child {
          font-size: 20px;
          font-weight: 700;
          display: block;
          width: 40%;
          margin: 8px auto 0;
        }
        span:nth-child(2) {
          position: absolute;
          right: 18px;
          bottom: 30px;
          font-size: 14px;
        }
        span:last-child {
          font-size: 14px;
          position: absolute;
          right: 18px;
          bottom: 6px;
        }
      }
    }
  }
  .vip_box {
    margin: 18px auto;
    width: 96%;
    li {
      width: 33%;
      text-align: center;
      float: left;
      p {
        font-size: 14px;
      }
      span {
        color: #f34040;
        display: block;
      }
      img {
        display: inline-block;
        margin: 6px auto;
        width: 58px;
        height: 58px;
        border-radius: 50%;
        border: 1px solid #ccc; /*no*/
      }
    }
    li:last-child {
      span {
        height: 78px;
        line-height: 78px;
        font-size: 18px;
        font-weight: 700;
      }
    }
  }
  .zhifu {
    width: 90%;
    margin: 0 auto;
    padding-bottom: 28px;
    font-size: 14px;
    img {
      width: 100%;
      display: block;
    }
    p {
      height: 22px;
      line-height: 22px;
    }
    h6 {
      height: 22px;
      line-height: 22px;
      color: #f34040;
      margin-bottom: 6px;
    }
  }
}
</style>



