<template>
  <div class="box">
    <h5><span @click="$router.go(-1)" class="el-icon-back"></span>收货信息</h5>
    <div class="city">
      <citycom
        :province_ids="updata_data.province"
        :city_ids="updata_data.city"
        :district_ids="updata_data.district"
        v-on:province_id="province_ids"
        v-on:city_id="city_ids"
        v-on:district_id="district_ids"
      ></citycom>
      <div class="xiangxi">
        <span>详细地址&nbsp;:</span>
        <el-input
          v-model="detailed"
          placeholder="请输入详细地址"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>联系人&nbsp;:</span>
        <el-input
          v-model="real_name"
          placeholder="请输入联系人姓名"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>联系电话&nbsp;:</span>
        <el-input
          v-model="mobile"
          placeholder="请输入联系人电话"
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
    </div>
    <el-button
      @click="updata_city"
      type="warning"
      style="display:block;width:80%;margin:28px auto;"
    >确&nbsp;&nbsp;认</el-button>
  </div>
</template>


<script>
import citycom from "@/components/city/city";
export default {
  name: "Vip",
  data() {
    return {
      province: null,
      city: null,
      district: null,
      detailed: null,
      updata_data: {
        province: null,
        city: null,
        district: null
      },
      real_name: null,
      mobile: null
    };
  },
  components: {
    citycom
  },
  created() {
    this.get_user();
  },
  mounted() {},
  methods: {
    updata_city() {
      var that = this;
      let data = { data: { token: localStorage.getItem("token") } };
      data.data.real_name = that.real_name;
      data.data.mobile = that.mobile;
      data.data.province = that.province;
      data.data.district = that.district;
      data.data.city = that.city;
      data.data.detailed = that.detailed;
      that.axios.post("user/user_address/create", data).then(res => {
        if (res.data.status) {
          that.$message({
          message: res.data.message,
          type: 'success'
        });
        } else {
          that.$message.error(res.data.message);
        }
      });
    },
    get_user() {
      var that = this;
      that.axios
        .post("user/user_address/create", {
          data: { token: localStorage.getItem("token") }
        })
        .then(res => {
          that.real_name = res.data.data.real_name ==0 ? null : res.data.data.real_name;
          that.mobile = res.data.data.mobile == 0 ? null : res.data.data.mobile ;
          that.province = res.data.data.province;
          that.city = res.data.data.city;
          that.district = res.data.data.district;
          that.detailed = res.data.data.detailed == 0 ? null :  res.data.data.detailed;
          that.updata_data.province = res.data.data.province;
          that.updata_data.city = res.data.data.city;
          that.updata_data.district = res.data.data.district;
        });
    },
    //城市三级联动
    province_id(value) {
      this.province = value;
    },
    city_id(value) {
      this.city = value;
    },
    district_id(value) {
      this.district = value;
    },
    //修改的三级联动
    province_ids(value) {
      this.updata_data.province = value;
      this.province = value;
    },
    city_ids(value) {
      this.updata_data.city = value;
      this.city = value;
    },
    district_ids(value) {
      this.updata_data.district = value;
      this.district = value;
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
     position: relative;
    span{
        position: absolute;
        top: 0;
        left: 12px;
        height: 38px;
        line-height: 38px;
        font-size: 18px;
        font-weight: 700;
    }
  }
  .city {
    width: 92%;
    margin: 48px auto;
    font-size: 14px;
    text-align: left;
    .xiangxi {
      margin: 6px auto;
      span {
        display: inline-block;
        width: 22%;
      }
    }
  }
}
</style>



