<template>
  <div class="box"></div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {};
  },
  created() {
    var that = this;
    //此接口 
    //初次进入两种情况  1.用户有token  (就是授权过)  2. 未授权
    that.axios.post("login", {}).then(res => { 
      //2 
      if (that.$route.query.token) { //授权了的用户   
            localStorage.setItem("token",that.$route.query.token);
           that.$router.push({ path: "/home/goodslis" });
      } else { //未授权的用户
      //第一步  
        if (res.data.status == 1) {
          // res.data.data 是后台返回地址路径(微信拉授权的路径)
          window.location.href = res.data.data;
        } 
      }
    });
  }
};
</script>
<style lang="scss" scoped>
</style>



