<template>
  <div class="box">
    <h5><span @click="$router.go(-1)" class="el-icon-back"></span>下级会员列表</h5>
    <el-table :data="tableData" style="width: 96%;margin:12px auto 0" >
      <el-table-column prop="date" label="加入日期" :width="tableWidth" fixed>
        <template slot-scope="scope">
            {{scope.row.updated_at | time}}
        </template>
      </el-table-column>
      <el-table-column prop="name" label="会员头像"  :width="tableWidth">
        <template slot-scope="scope">
            <img class="touxiang" :src="scope.row.avater" alt="">
        </template>
      </el-table-column>
      <el-table-column prop="name" label="下级名称" :width="tableWidth"></el-table-column>
    </el-table>
     <div class="page" >
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
    </div>
  </div>
</template>
<script>
import pagecon from '@/components/page/pagecon'

export default {
  name: "Viplis",
  data() {
    return {
      num: null,
       pages: null,
      tableWidth: window.innerWidth * 0.45
      ,tableData: []
      
    };
  },
  created() {
    this.get_lis();
  },
   components: {
    pagecon
  },
  mounted() {},
  methods: {
    get_lis() {
       var that = this ;
        let data = {data:{token:localStorage.getItem('token')}};
        that.axios.post('/user/children_list',data).then(res=>{
             that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
        })
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  .page{
    width: 90%;
    margin: 12px  auto ;
  }
  .touxiang{
    display: block;
    width: 38px;
    height: 38px;
    margin: 0 auto;
    border-radius: 50%;
  }
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
    position: relative;
    span{
        position: absolute;
        top: 0;
        left: 12px;
        height: 38px;
        line-height: 38px;
        font-size: 18px;
        font-weight: 700;
    }
  }
}
</style>



