<template>
  <div class="box">
    <div class="top" v-html="cont"></div>
    <div class="nodata" v-if="data_lis.length==0">请还没有选择商品,请前往商城页面选择您喜欢的商品吧!</div>
    <div class="tihuo" v-for="(items,index) in data_lis" :key="index">
      <p>
        <i @click="del_data(items.id,index)" class="el-icon-delete" style="color:#f53737"></i>
        <span>产品&nbsp;:</span>{{items.name}}
      </p>
      <p v-if="items.parameters_values!=0">
        <i></i>
        <span>{{items.parameters_name}}&nbsp;:</span>{{items.parameters_values}} &nbsp;&nbsp;
        <!-- &nbsp;&nbsp;{{items.plus_price}}    -->
      </p>
      <p>
        <i></i>
        <span>卡密&nbsp;:</span>
        <el-input placeholder="请输入卡密" @change="is_kalman(items.kalman,index,items.id)" v-model="items.kalman" size="mini" style="display:inline-block;width:70%" clearable></el-input>
      </p>
      <p>
        <span>价格&nbsp;:</span> 
        <b>￥{{items.saleconst}}</b> &nbsp;&nbsp;
        <span>保险金&nbsp;:</span>
        <b>￥{{items.retailprice}}</b>
      </p>
    <div class="ps">ps:单个商品对应一个卡密,卡密错误商品价格不会消失</div>
    </div>
    <div class="city">
      <citycom
        :province_ids="updata_data.province"
        :city_ids="updata_data.city"
        :district_ids="updata_data.district"
        v-on:province_id="province_ids"
        v-on:city_id="city_ids"
        v-on:district_id="district_ids"
      ></citycom>
      <div class="xiangxi">
        <span>详细地址&nbsp;:</span>
        <el-input
          v-model="detailed"
          placeholder="请输入详细地址"
          size="mini"
          style="display:inline-block;width:70%"
          clearable
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>收货人&nbsp;:</span>
        <el-input
          v-model="real_name"
          placeholder="请输入收货人姓名"
          size="mini"
          style="display:inline-block;width:70%"
          clearable
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>联系电话&nbsp;:</span>
        <el-input
          v-model="mobile"
          placeholder="请输入收货人电话"
          type="number"
          size="mini"
          style="display:inline-block;width:70%"
          clearable
        ></el-input>
      </div>
    </div>
    <div  v-if="data_lis.length!=0" class="heji">
      <span>商品总价&nbsp;:</span>
      <b>￥{{and_prit}}</b>
    </div>
    <div  v-if="data_lis.length!=0" class="heji">
      <span>保险费用&nbsp;:</span>
      <b>￥{{and_baoxian}}</b>
    </div>
    <div  v-if="data_lis.length!=0" class="heji1">
      <span style="color:#f53737;width:30%">合计金额&nbsp;:</span>
      <b>￥{{and_baoxian + and_prit}}</b>
    </div>
    <div  v-if="data_lis.length!=0">
    <el-button v-if="!type_show" @click="my_btn(2)" type="success" style="display:block;width:80%;margin:28px auto;" :disabled = 'iszhifu'>确认提货</el-button>
    <div v-if="type_show" style="width:90%;margin:0 auto;text-align:center">
      <el-button @click="my_btn(1)" type="danger" style="width:45%;margin:28px auto;" :disabled = 'iszhifu'>余额提货</el-button>
      <el-button @click="my_btn(2)" type="success" style="width:45%;margin:28px auto;" :disabled = 'iszhifu'>微信提货</el-button>
    </div>
    </div>
  </div>
</template>


<script>
import tihuo from "./tihuo";
export default tihuo;

</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;
  .top {
    width: 100%;
    border-bottom: 1px solid #ccc;/*no*/
  }
  .nodata{
    color: #f53737;
    font-size: 16px;
    width: 80%;
    margin: 38px auto ;
    height: 28px;
    line-height: 28px;
    font-weight: 700;
  }
  .ps {
    width: 92%;
    margin: 3px auto;
    font-size: 12px;
    color: #ccc;
  }
  .city {
    width: 92%;
    margin: 12px auto;
    font-size: 14px;
    text-align: left;
    .xiangxi {
      margin: 6px auto; 
      span{
          display: inline-block;
          width: 25%;
      }

    }
  }
  .tihuo {
    width: 92%;
    margin: 12px auto;
    border-bottom: 1px solid #ccc; /*no*/
    p {
      font-size: 14px;
      margin: 10px 0;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      i {
        width: 22px;
        min-height: 2px;
        display: inline-block;
        font-size: 20px;
      }
      span {
        display: inline-block;
        width: 16%;
        margin-right: 3px;
      }
      b {
        color: #f53737;
      }
    }
  }
  .heji {
    width: 92%;
    margin: 12px auto;
    font-size: 14px;
    span {
      display: inline-block;
      width: 20%;
    }
    b {
      color: #f53737;
    }
  }
  .heji1 {
      width: 92%;
      margin: 18px auto;
      font-size: 18px;
      span {
        display: inline-block;
        width: 20%;
      }
      b {
        color: #f53737;
      }
    }
}
</style>



