<template>
  <div class="box">
    <div class="search_box">
      <el-input v-model="keyword" placeholder="请输入内容" size="medium" style="display:inline-block;width:75%"></el-input>
      <el-button type="primary"  icon="el-icon-search" size="small" style="display:inline-block;" @click="get_lis" >搜索</el-button>
      <el-select  v-model="id" placeholder="请选择分类" size="medium" style="width:40%;margin-top:6px;"> 
        <el-option  v-for="(items,x) in category" :key="x" :label="items.name"  :value='items.id'></el-option>
      </el-select>
      <el-select v-model="order" placeholder="排序" size="medium" style="width:30%;margin-top:6px;"> 
         <el-option  v-for="(items,i) in order_type" :key="i" :label="items.name"  :value='items.id'></el-option>
      </el-select>
    </div>
    <!-- 商品展示 -->
    <div class="modle">
      <ul class="clearfix">
        <li @click="go_details(ites.id)" v-for="(ites,ii) in data_lis" :key="ii"  >
            <img :src="baseurl + ites.pic" alt>
            <p>
              <span>产品名称 : {{ites.name}}</span>
              <span>价格 : <i>{{ites.sale_const}}</i></span>
            </p>
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import goods from "./goods";
export default goods;
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  background: #f3f0f0;
  min-height: 580px;
  .search_box {
    width: 96%;
    margin: 0 auto;
    padding: 12px 0;
    background: #f3f0f0;
  }
  .modle {
    ul {
      li {
        width: 49%;
        float: left;
          img {
            width: 90%;
            // min-height: 88px;
            // max-height: 128px;
            height:108px ;
            display: block;
            margin: 0 auto 6px;
          }
          p {
            width: 90%;
            margin: 12px auto;
            font-size: 12px;
            span {
              display: block;
              font-size: 14px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            i {
              margin-top: 4px;
              display: inline-block;
              color: #ff004c;
            }
          
        }
      }
    }
  }
}
</style>



