<template>
    <div >
        <div v-if='pages' style="margin:3px ;" >总共{{pages.total}}条</div>
        <el-pagination v-if='pages'  :pager-count='5' layout=" prev, pager, next"
            :current-page="pages.current_page"  :total="pages.total" :page-size="pages.per_page"  :page-sizes="[pages.per_page]" 
            @current-change="get_lis" @size-change="get_lis">
        </el-pagination>
        <!-- <div v-if="!pages" style="text-aglin:center;">对不起,暂无数据</div> -->
    </div>
</template>

<script>
export default {
  name: "pagecon",
  data() {
    return {};
  },
  props: {
    pages: {}
  },
  created() {},
  computed: {}
  ,methods: {
    get_lis(e) {
      this.$parent.get_lis(e);
    }
  }
};
</script>


<style scoped>
</style>



