<template>
  <div class="box">
    <!-- 轮播图 -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(items,index) in banners" :key="index" @click="go_banners(items.url)" >
          <img :src="baseurl+items.pic" alt style="width:100%">
        </div>
      </div>
    </div>
    <!-- f分类跳转 -->
    <ul class="nav clearfix">
      <li v-for="(item,ss) in category" :key="ss" @click="go_goods(item.id)">
        <img :src="baseurl + item.cover_pic" alt>
        <p>{{item.name}}</p>
      </li>
    </ul>
    <!-- 部分商品展示 -->
    <div class="modle" v-for="(items,ii) in prodcut" :key="ii">
      <ul class="clearfix">
        <li >
          <img :src="baseurl + items.cover_pic" alt>
          <span>{{items.title}}</span>
        </li>
        <li @click="go_details(ites.id)" v-for="(ites,ind) in items.product" :key="ind" >
          <img :src="baseurl + ites.cover" alt>
          <p>
            <span>{{ites.name}}</span>
            <i>￥{{ites.sale_const}}</i>
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import goodslis from "./goodslis";
export default goodslis;
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  background: #f3f0f0;
  .swiper-container {
    width: 100%;
    margin: 0 auto;
  }
  .nav {
    width: 92%;
    margin: 18px auto; /*no*/
    li {
      float: left;
      width: 25%;
      text-align: center;
      margin-top: 12px;
      img {
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
      p {
        height: 20px;
        line-height: 20px;
        font-size: 14px;
      }
    }
  }
  .modle {
    ul {
      li:first-child {
        height: 42px;
        line-height: 42px;
        width: 100% ;
        background: #e2dfdf8f;
        margin-bottom: 12px; /*no*/
        img {
          width: 30px;
          height: 30px;
          display: inline-block;
          vertical-align: middle;
          margin-right: 8px;
          margin-left:28px; 
        }
        span {
          font-size: 16px;
          display: inline-block;
        }
      }
      li {
        width: 50%;
        float: left;
        img {
          width: 128px;
          height: 88px;
          display: block;
          margin: 0 auto 6px;
        }
        p {
          width: 76%;
          margin: 12px auto;
          span {
            display: block;
            font-size: 14px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          i {
            margin-top: 4px;
            display: block;
            font-size: 14px;
            color: #ff004c;
          }
        }
      }
    }
  }
}
</style>



