<template>
  <div class="box">
    <h5><span @click="$router.go(-1)" class="el-icon-back"></span>余额记录</h5>
     <el-table :data="tableData" style="width: 96%;margin:12px auto 0"  >
      <el-table-column prop="date" label="日期" :width="tableWidth" fixed >
        <template slot-scope="scope">
            {{scope.row.created_at | time}}
        </template>
      </el-table-column>
      <el-table-column prop="number" label="操作余额" :width="tableWidth"></el-table-column>
      <el-table-column prop="source_user" label="来源" :width="tableWidth" ></el-table-column>
      <el-table-column prop="modesa" label="使用方式" :width="tableWidth"></el-table-column>
      <el-table-column prop="type" label="类型" :width="tableWidth"></el-table-column>
    </el-table>
     <div class="page" >
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
    </div>

  </div>
</template>
<script>
import pagecon from '@/components/page/pagecon'

export default {
  name: "Balancelis",
  data() {
    return {
      num: null,
      pages: null,
      tableWidth: window.innerWidth * 0.45,
      tableData: []
    };
  },
  created() {this.get_lis();},
  components: {
    pagecon
  },
  mounted() {},
  methods: {
    get_lis(e) {
      var that = this ;
        let data = {data:{token:localStorage.getItem('token')},page:e };
        that.axios.post('/user/balances',data).then(res=>{
            that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
        })
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  .page{
    width: 90%;
    margin: 12px  auto ;
  }
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
    position: relative;
    span{
        position: absolute;
        top: 0;
        left: 12px;
        height: 38px;
        line-height: 38px;
        font-size: 18px;
        font-weight: 700;
    }
  }
}
</style>



