import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/home.vue'
import Login from '@/components/home/login.vue'
import Goodslis from '@/components/goodslis/goodslis.vue'
import Users from '@/components/users/users.vue'
import User_data from '@/components/users/user_data.vue'
import Goods from '@/components/goods/goods.vue'
import Vip from '@/components/vip/vip.vue'
import Viplis from '@/components/vip/viplis.vue'
import Add_vip from '@/components/vip/add_vip.vue'
import Balancelis from '@/components/vip/balancelis.vue'
import Agreement from '@/components/vip/agreement.vue'
import Details from '@/components/details/details.vue'
import Tihuo from '@/components/tihuo/tihuo.vue'
import Kalman from '@/components/tihuo/kalman.vue'
import Huanhuolis from '@/components/tihuo/huanhuolis.vue'
import Tihuolis from '@/components/tihuo/tihuolis.vue'
import Huanhuobanli from '@/components/tihuo/huanhuobanli.vue'
import Dizhi from '@/components/dizhi/dizhi.vue'
import Shouhou from '@/components/shouhou/shouhou.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', redirect: '/home' }
    , {
      path: '/home', name: 'Home', meta: { requireAuth: true }, component: Home
      , children: [
        { path: 'goodslis', name: 'Goodslis', meta: { requireAuth: true }, component: Goodslis }
        , { path: 'users', name: 'User', meta: { requireAuth: true }, component: Users }
        , { path: '/goods', name: 'Goods', meta: { requireAuth: true }, component: Goods }
        , { path: '/vip', name: 'Vip', meta: { requireAuth: true }, component: Vip }
        , { path: '/viplis', name: 'Viplis', meta: { requireAuth: true }, component: Viplis }
        , { path: '/balancelis', name: 'Balancelis', meta: { requireAuth: true }, component: Balancelis }
        , { path: '/agreement', name: 'Agreement', meta: { requireAuth: true }, component: Agreement }
        , { path: '/details', name: 'Details', meta: { requireAuth: true }, component: Details }
        , { path: 'tihuo', name: 'Tihuo', meta: { requireAuth: true }, component: Tihuo }
        , { path: 'dizhi', name: 'Dizhi', meta: { requireAuth: true }, component: Dizhi }
        , { path: 'user_data', name: 'User_data', meta: { requireAuth: true }, component: User_data }
        , { path: 'shouhou', name: 'Shouhou', meta: { requireAuth: true }, component: Shouhou }
        , { path: '/kalman', name: 'Kalman', meta: { requireAuth: true }, component: Kalman }
        , { path: 'tihuolis', name: 'Tihuolis', meta: { requireAuth: true }, component: Tihuolis }
        , { path: 'huanhuolis', name: 'Huanhuolis', meta: { requireAuth: true }, component: Huanhuolis }
        , { path: 'huanhuobanli', name: 'Huanhuobanli', meta: { requireAuth: true }, component: Huanhuobanli }
        , { path: '/add_vip', name: 'Add_vip', meta: { requireAuth: true }, component: Add_vip }
        , { path: '/login', name: 'Login', component: Login }
      ]
      , redirect: '/home/goodslis'
    }

    , { path: '**', redirect: '/home/goodslis' }
  ]
})



