<template>
  <div class="box">
    <div class="top">
      <div class="user_box">
        <img :src="user_data.avater" alt="">
        <div>
          <p>{{user_data.name}}</p>
          <p>注册时间:{{user_data.created_at | time}}</p>
          <!-- <p>推荐人:XXXXXXXX</p> -->
        </div>
      </div>
    </div>
    
    <ul class="dings clearfix">
      <li><router-link to='/home/goodslis'><img src="../../../static/images/jiayiguanli.png" alt=""><p>交易管理</p></router-link></li>
      <!-- <li><router-link to='/home/goodslis'><img src="../../../static/images/goumai.png" alt=""><p>购买列表</p></router-link></li> -->
      <li><router-link to='/home/tihuo'><img src="../../../static/images/mingxi.png" alt=""><p>待完成</p></router-link></li>
      <li><router-link to='/home/huanhuobanli'><img src="../../../static/images/banli.png" alt=""><p>换货办理</p></router-link></li>
    </ul>
    <!-- <ul class="dings clearfix">
      <li><router-link to='/home/goodslis'><img src="../../../static/images/fensi.png" alt=""><p>粉丝关系</p></router-link></li>
      <li @click="off"><router-link to=''><img src="../../../static/images/erweima.png" alt=""><p>分享二维码</p></router-link></li>
    </ul> -->
    <ul class="dings clearfix">
      <li><router-link to='/home/goodslis'><img src="../../../static/images/dingdan.png" alt=""><p>订单管理</p></router-link></li>
      <li><router-link to='/home/tihuolis'><img src="../../../static/images/dingdan.png" alt=""><p>提货明细</p></router-link></li>
      <li><router-link to='/home/huanhuolis'><img src="../../../static/images/huanhuo.png" alt=""><p>换货记录</p></router-link></li>
    </ul>
    <ul class="dings clearfix">
      <li><router-link to='/home/goodslis'><img src="../../../static/images/anquan.png" alt=""><p>安全中心</p></router-link></li>
      <li><router-link to='/home/user_data'><img src="../../../static/images/user_lis.png" alt=""><p>个人信息</p></router-link></li>
      <li><router-link to='/home/dizhi'><img src="../../../static/images/dizhi.png" alt=""><p>地址管理</p></router-link></li>
      <li @click="off"><router-link to=''><img src="../../../static/images/erweima.png" alt=""><p>分享二维码</p></router-link></li>
      <li @click="del_token" ><router-link to=''><img src="../../../static/images/del.png" alt=""><p>校准参数</p></router-link></li>
    </ul>
    <div class="erweima" v-if="show">
      <span @click="off" class="el-icon-error"></span>
      <img :src="img_url" alt="">
    </div>
  </div>
</template>


<script>
import users from "./users";
export default users;
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;
  .erweima{
    width: 100%;
    height: 100%;
    z-index: 10000;
    max-width: 750px;/*no*/
    background: rgba(105, 104, 104, 0.781);
    left: 0;
    top: 0;
    position: fixed;
    img{
      display: block;
      height: 88%;
      margin: 18px auto 0;
    }
    span{
      position: absolute;
      right: 8px;
      top: 8px;
      font-size: 32px;
      color: #e43c3c;
    }

  }
  .top{
    margin:18px auto 0 ; 
    height: 168px;
    width: 96%;
    border-radius: 18px;
    background: url('../../../static/images/bg.png') no-repeat;
    background-size:100% 100%;
    .user_box{
      position: relative;
      height: 100%;
      width: 100%;
      img{
        width: 88px;
        height: 88px;
        border-radius: 100%;
        position: absolute;
        top: 28px;
        left: 28px;
      }
      div{
        position: absolute;
        top: 38px;
        left:128px;
        font-size: 16px;
        font-weight: 700;
        color: #fff;
        p{
          margin-top:12px; 
        }
        p:last-child{
          margin-top: 22px;
          font-size: 14px;
        }
      }
    } 
  }
  .dings{
      width: 96%;
      margin: 12px auto;
      border-radius: 6px;
      li:first-child{
        width: 100%;
        font-size: 14px;
        font-weight: 400;
        border-bottom: 1px solid #f2f2f2;/*no*/
        text-align: left;
        img{
          width: 28px;
          height: 28px;
          margin: 6px 12px;
          display: inline-block;
          vertical-align: middle;
        }
        p{
          display: inline-block;
        }
      }
      li{
        width: 25%;
        text-align: center;
        float: left;
        a{
          display: block;
          width: 100%;
          height: 100%;
        }
        img{
          width: 30px;
          height: 30px;
          display: inline-block;
          margin:6px 0 ; 
        }
        p{
          padding-bottom:6px; 
        }
      }
    }
}
</style>



