
export default {
  name: "Goods",
  data() {
    return {
      data_lis: {} //全部数据
      , id: null    //所在分类id
      , order: null    //
      , category: []    //type数组
      , order_type: []    //order数组
      , keyword: null    //搜索内容
      , baseurl: ''
    };
  },
  created() {
    var that = this;
    this.baseurl = this.axios.defaults.baseURL;
    this.id = that.$route.query.id;
    that.get_lis();
    that.get_type();
  },
  mounted() {

  },
  methods: {
    get_lis() {
      var that = this;
      let data = { data: { token: localStorage.getItem('token'), category: that.id, keyword: that.keyword ,search_type :'name' ,order:that.order} };
      that.axios.post('/product/category', data).then(res => {
        that.data_lis = res.data.data.product;
        console.log(that.data_lis)
      })
    }

    , get_type() {
      var that = this;
      let data = { data: { token: localStorage.getItem('token'), type: 1 } };
      that.axios.post('/get_screen', data).then(res => {
        that.category.push({ id: 0, name: '全部分类' })                                  //栏目处理
        for (var i = 0; i < res.data.data.category.length; i++) {                       //顶级
          that.category.push({ id: res.data.data.category[i].id, name: res.data.data.category[i].name })
          if (res.data.data.category[i].children) {
            for (var e = 0; e < res.data.data.category[i].children.length; e++) {   //二级
              that.category.push({ id: res.data.data.category[i].children[e].id, name: '---' + res.data.data.category[i].children[e].name })
              for (var s = 0; s < res.data.data.category[i].children[e].children.length; s++) { //三级
                that.category.push({ id: res.data.data.category[i].children[e].children[s].id, name: '------' + res.data.data.category[i].children[e].children[s].name })
              }
            }
          }
          that.order_type.push({ name: '默认排序', id: 0 })
          for (let i in res.data.data.order) {//默认排序
            that.order_type.push({ name: res.data.data.order[i], id: i })
          }
        }
      })
    }
    ,go_details(id){
      this.$router.push({path:'/details',query:{id:id}})
    }
  }
};


