import citycom from "@/components/city/city";
import http from "../../api/http";
export default {
    name: "Vip",
    data() {
        return {
            number: 0,
            tableData: []
            , price: null
            , tongyi: null
            , user_data: {}
            , isshow: false
            , vipshow: 1
            , tixian_type: null
            , province: null,
            city: null,
            district: null,
            detailed: null,
            updata_data: {}
            , data_lis: {}
            , real_name: null
            , mobile: null
            , iszhifu: false
            , url: ''
        };
    },
    created() {
        this.url = this.axios.defaults.baseURL;
        this.get_user();
        this.get_dizhi();
    
    },
    mounted() { },
    components: {
        citycom
    },
    methods: {
        get_user() {
            var that = this;
            let data = { data: { token: localStorage.getItem('token') } };
            that.axios.post('/user/distribution', data).then(res => {
                console.log(res.data)
                that.user_data = res.data.data
                that.vipshow = !res.data.data.type;
                if ( !res.data.data.type || res.data.data.is_limit==1) {
                    that.$router.push({ path: '/home/users' })
                }
            })
        }
        , my_btn(type) {
            var that = this;
            if (that.tongyi == null) {
                that.$message.error('请先阅读商城协议');
                return;
            }
            if (that.number == 0) {
                that.$message.error('购买数量不能为0');
                return;
            }
            that.iszhifu = true;
            setTimeout(function () {
                that.iszhifu = false;
            }, 4000)
            let data = { data: { token: localStorage.getItem('token'), type, number: that.number } };
            that.axios.post('user/kalaman/create', data).then(res => {

                if (type == 1) { //余额支付
                    if (res.data.status) {
                        that.$message({ message: res.data.message, type: 'success' });
                        that.get_dizhi();
                        that.get_user();
                    } else {
                        that.$message.error(res.data.message);
                        return;
                    }
                } else { //微信支付
                    if (res.data.status) {
                        // 存储微信支付数据data
                        let data = res.data.data
                        console.log(res.data.data)
                        console.log('即将跳转微信支付');
                        http.wexinPay({
                            appId: data.appId,
                            nonceStr: data.nonceStr,
                            package: data.package,
                            paySign: data.paySign,
                            signType: data.signType,
                            timeStamp: data.timeStamp
                        }, res => {
                            that.get_dizhi();
                            that.get_user();
                            that.$message({
                                message: '支付成功',
                                type: 'success'
                            });
                        })

                    } else { that.$message.error(res.data.message); }
                }
            })
        }
        , is_tixian() {
            var that = this;
            if (that.price == null || that.price == 0) {
                that.$message.error('请输入提现金额');
                return;
            }
            if (that.price < 10) {
                that.$message.error('提现金额不能小于10');
                return;
            }
            if (that.tixian_type == null) {
                that.$message.error('请选择提现类型');
                return;
            }
            let data = { data: { token: localStorage.getItem('token'), type: that.tixian_type, price: that.price } }
            that.axios.post('/user/tixian/create', data).then(res => {
                if (res.data.status) {
                    that.$message({ message: '提现申请成功', type: 'success' });
                    that.get_user();
                } else {
                    that.$message.error(res.data.message);
                }
            })

        }
        , handleChange() { },
        go_url() {
            this.$router.push({ path: "/viplis", query: { id: 123 } });
        },
        go_kalman() {
            this.$router.push({ path: "/kalman", query: { id: 123 } });
        },
        go_balancelis() {
            this.$router.push({ path: "/balancelis", query: { id: 123 } });
        },
        go_agreement() {
            this.$router.push({ path: "/agreement" });
        },
        show() {
            let that = this;
            this.price = null;
            that.isshow = !that.isshow;
        }
        , proving() {
            // this.price = this.price.replace(/[^\.\d]/g, ''); //为正数
            // this.price = this.price.replace('.', ''); //不保留小数点
            var num = this.user_data.balances * 1
            if (this.price > num) {
                this.price = num;
            }
            if (this.price % 10 != 0) {
                if (this.price > 10) {
                    this.price = this.price - this.price % 10;
                }
            }

        }
        , get_dizhi() {
            var that = this;
            that.axios
                .post("user/user_address/create", {
                    data: { token: localStorage.getItem("token") }
                })
                .then(res => {
                    console.log(res)
                    that.real_name = res.data.data.real_name == 0 ? null : res.data.data.real_name;
                    that.mobile = res.data.data.mobile == 0 ? null : res.data.data.mobile;
                    that.province = res.data.data.province;
                    that.city = res.data.data.city;
                    that.district = res.data.data.district;
                    that.detailed = res.data.data.detailed == 0 ? null : res.data.data.detailed;
                    that.updata_data.province = res.data.data.province;
                    that.updata_data.city = res.data.data.city;
                    that.updata_data.district = res.data.data.district;
                });
        }
        //城市三级联动
        , province_id(value) {
            this.province = value;
        },
        city_id(value) {
            this.city = value;
        },
        district_id(value) {
            this.district = value;
        },
        //修改的三级联动
        province_ids(value) {
            this.updata_data.province = value;
            this.province = value;
        },
        city_ids(value) {
            this.updata_data.city = value;
            this.city = value;
        },
        district_ids(value) {
            this.updata_data.district = value;
            this.district = value;
        }
    }
};


