import citycom from "@/components/city/city";
import http from "../../api/http";
export default {
    name: "Vip",
    data() {
        return {
            cont: null, //顶部内容
            province: null,
            city: null,
            district: null,
            detailed: null,
            updata_data: {}
            , data_lis: []
            , real_name: null
            , mobile: null
            , and_prit: 0 //zong
            , and_baoxian: 0 //zong
            , type_show: false
            , iszhifu: false
        };
    },
    components: {
        citycom
    },
    created() {
        this.get_lis();
        this.get_user();
    },
    mounted() { },
    methods: {
        get_lis() {
            var that = this;
            let data = { data: { token: localStorage.getItem('token') } };
            that.and_prit = 0;
            that.and_baoxian = 0;
            that.axios.post('user/tack_goods', data).then(res => {
                that.data_lis = res.data.data.product
                that.cont = res.data.data.tihuo_wenben;
                that.type_show = (res.data.data.type==1 && res.data.data.is_limit==0);

                for (let i in that.data_lis) {
                    if (that.data_lis[i].kalman) {
                        that.data_lis[i].saleconst = 0;
                    }
                    that.and_prit += that.data_lis[i].saleconst * 1;
                    that.and_baoxian += that.data_lis[i].retailprice * 1;
                }
            })
        }
        , updata_city() {
            var that = this;
            let data = { data: { token: localStorage.getItem("token") } };
            data.data.real_name = that.real_name;
            data.data.mobile = that.mobile;
            data.data.province = that.province;
            data.data.district = that.district;
            data.data.city = that.city;
            data.data.detailed = that.detailed;
            that.axios.post("user/user_address/create", data).then(res => {
                if (res.data.status) {

                } else {
                    that.$message.error(res.data.message);
                }
            });
        },
        get_user() {
            var that = this;
            that.axios
                .post("user/user_address/create", {
                    data: { token: localStorage.getItem("token") }
                })
                .then(res => {
                    that.real_name = res.data.data.real_name == 0 ? null : res.data.data.real_name;
                    that.mobile = res.data.data.mobile == 0 ? null : res.data.data.mobile;
                    that.province = res.data.data.province;
                    that.city = res.data.data.city;
                    that.district = res.data.data.district;
                    that.detailed = res.data.data.detailed == 0 ? null : res.data.data.detailed;
                    that.updata_data.province = res.data.data.province;
                    that.updata_data.city = res.data.data.city;
                    that.updata_data.district = res.data.data.district;
                });
        },
        del_data(id, index) { //删除商品
            var that = this;
            let data = { data: { token: localStorage.getItem('token'), id: id } };
            that.axios.post('/user/tack_goods/destroy', data).then(res => {
                if (res.data.status) {
                    that.$message({
                        message: res.data.message,
                        type: 'success'
                    });
                    that.get_lis();
                } else {
                    that.$message.error(res.data.message);
                }
            })
        }
        , is_kalman(kalman, index, id) {
            var that = this;
            for (let i in that.data_lis) {
                if (that.data_lis[i].kalman == kalman && i != index && kalman != '') {
                    that.$message.error('卡密重复使用');
                    that.data_lis[index].kalman = null;
                    return;
                }

            }
            let data = { data: { token: localStorage.getItem('token'), kalman, id } }
            that.axios.post('/user/kalaman/verification_kalaman', data).then(res => {
                if (res.data.status) {
                    that.data_lis[index].saleconst = '0.00';
                    that.and_prit = 0;
                    that.and_baoxian = 0
                    that.data_lis.forEach(ele => {
                        that.and_prit += ele.saleconst * 1;
                        that.and_baoxian += ele.retailprice * 1;
                    });
                } else {
                    that.$message.error('卡密错误');
                    that.get_lis();
                }
            })
        }
        , my_btn(type) {  //提交
            var that = this;
            that.iszhifu = true ;
            that.updata_city();
            let data = {
                data: {
                    token: localStorage.getItem('token')
                    , real_name: that.real_name
                    , mobile: that.mobile
                    , province: that.province
                    , district: that.district
                    , city: that.city
                    , detailed: that.detailed
                    , type
                }
            };
            setTimeout(function(){
                that.iszhifu = false ;
            },4000)
            that.axios.post('/user/order/create', data).then(res => {
                console.log(res)
                if (type == 1) { //余额支付
                    if (res.data.status) {
                        that.$message({ message: res.data.message, type: 'success' });
                        that.get_lis();
                        that.get_user();
                        that.and_baoxian = 0;
                        that.and_prit = 0;
                    } else {
                        that.$message.error(res.data.message);
                        return;
                    }
                } else {  //微信支付
                    if (res.data.status) {
                        // 存储微信支付数据data
                        let data = res.data.data
                        console.log(res.data.data)
                        console.log('即将跳转微信支付');
                        http.wexinPay({
                            appId: data.appId,
                            nonceStr: data.nonceStr,
                            package: data.package,
                            paySign: data.paySign,
                            signType: data.signType,
                            timeStamp: data.timeStamp
                        }, res => {
                            that.get_lis();
                            that.$message({
                                message: '支付成功',
                                type: 'success'
                            });
                        })

                    } else { that.$message.error(res.data.message); }
                }
            })

        }
        //城市三级联动
        , province_id(value) {
            this.province = value;
        },
        city_id(value) {
            this.city = value;
        },
        district_id(value) {
            this.district = value;
        },
        //修改的三级联动
        province_ids(value) {
            this.updata_data.province = value;
            this.province = value;
        },
        city_ids(value) {
            this.updata_data.city = value;
            this.city = value;
        },
        district_ids(value) {
            this.updata_data.district = value;
            this.district = value;
        }
    }
};


