<template>
  <div class="box">
    <h5><span @click="$router.go(-1)" class="el-icon-back"></span>会员信息</h5>
    <div class="city" >
      <div class="xiangxi">
        <span>真实姓名&nbsp;:</span>
        <el-input
          v-model="data_lis.user.real_name"
          placeholder="请输入真实姓名"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>联系电话&nbsp;:</span>
        <el-input
          v-model="data_lis.user.mobile" 
          placeholder="请输入联系人电话"
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>微信号&nbsp;:</span>
        <el-input
          v-model="data_lis.coffers.wechat"
          placeholder="请输入微信号"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>银行账户&nbsp;:</span>
        <el-input
          v-model="data_lis.coffers.bank"
          placeholder="请输入银行账户"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>开户名&nbsp;:</span>
        <el-input
          v-model="data_lis.coffers.bank_name"
          placeholder="请输入开户名"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>开户行&nbsp;:</span>
        <el-input
          v-model="data_lis.coffers.bank_type"
          placeholder="请输入开户行"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>开户网点&nbsp;:</span>
        <el-input
          v-model="data_lis.coffers.net_point"
          placeholder="请输入开户网点"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi" style="margin:12px 0">
        我所在的区域&nbsp;:
      </div>
      <citycom
        :province_ids="updata_data.province"
        :city_ids="updata_data.city"
        :district_ids="updata_data.district"
        v-on:province_id="province_ids"
        v-on:city_id="city_ids"
        v-on:district_id="district_ids"
      ></citycom>
      <div class="xiangxi">
        <span>详细地址&nbsp;:</span>
        <el-input
          v-model="detailed"
          placeholder="请输入详细地址"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
    </div>
    <el-button
      @click="updata_city"
      type="warning"
      style="display:block;width:80%;margin:28px auto;"
    >确&nbsp;&nbsp;认</el-button>
  </div>
</template>


<script>
import citycom from "@/components/city/city";
export default {
  name: "User_data",
  data() {
    return {
      province: null,
      city: null,
      district: null,
      detailed: null,
      updata_data: {
        province: null,
        city: null,
        district: null
      },
      data_lis:{user:{},coffers:{}}
    };
  },
  components: {
    citycom
  },
  created() {
    this.get_user();
  },
  mounted() {},
  methods: {
    updata_city() {
        var that = this;
        that.data_lis.user.province = that.province;
        that.data_lis.user.city = that.city;
        that.data_lis.user.district = that.district  ;
        that.data_lis.user.detailed = that.detailed;
        that.data_lis.user.province = that.updata_data.province;
        that.data_lis.user.city = that.updata_data.city;
        that.data_lis.user.district = that.updata_data.district;
        let data = {};
        data.data = that.data_lis;
        data.data.token = localStorage.getItem('token');
        that.axios.post("/user/show", data).then(res => {
            if (res.data.status) {
            that.$message({
                message: res.data.message,
                type: "success"
            });
            } else {
            that.$message.error(res.data.message);
            }
        });
    },
    get_user() {
      var that = this;
      that.axios
        .post("/user/show", {
          data: { token: localStorage.getItem("token") }
        })
        .then(res => {
            
            that.data_lis = res.data.data;
            that.data_lis.user.real_name = that.data_lis.user.real_name == 0 ?null :that.data_lis.user.real_name;
            that.data_lis.user.mobile = that.data_lis.user.mobile == 0 ?null :that.data_lis.user.mobile;
            that.data_lis.coffers.wechat = that.data_lis.coffers.wechat == 0 ?null :that.data_lis.coffers.wechat;
            that.data_lis.coffers.bank = that.data_lis.coffers.bank == 0 ?null :that.data_lis.coffers.bank;
            that.data_lis.coffers.bank_name = that.data_lis.coffers.bank_name == 0 ?null :that.data_lis.coffers.bank_name;
            that.data_lis.coffers.bank_type = that.data_lis.coffers.bank_type == 0 ?null :that.data_lis.coffers.bank_type;
            that.data_lis.coffers.net_point = that.data_lis.coffers.net_point == 0 ?null :that.data_lis.coffers.net_point;

            that.province = that.data_lis.user.province;
            that.city = that.data_lis.user.city;
            that.district = that.data_lis.user.district;
            that.detailed = that.data_lis.user.detailed==0?null:that.data_lis.user.detailed;
            that.updata_data.province = that.data_lis.user.province;
            that.updata_data.city = that.data_lis.user.city;
            that.updata_data.district = that.data_lis.user.district;
        });
    },
    //城市三级联动
    province_id(value) {
      this.province = value;
    },
    city_id(value) {
      this.city = value;
    },
    district_id(value) {
      this.district = value;
    },
    //修改的三级联动
    province_ids(value) {
      this.updata_data.province = value;
      this.province = value;
    },
    city_ids(value) {
      this.updata_data.city = value;
      this.city = value;
    },
    district_ids(value) {
      this.updata_data.district = value;
      this.district = value;
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
    position: relative;
    span{
        position: absolute;
        top: 0;
        left: 12px;
        height: 38px;
        line-height: 38px;
        font-size: 18px;
        font-weight: 700;
    }
  }
  .city {
    width: 92%;
    margin: 48px auto;
    font-size: 14px;
    text-align: left;
    .xiangxi {
      margin: 6px auto;
      span {
        display: inline-block;
        width: 22%;
      }
    }
  }
}
</style>



