import Swiper from "swiper";
export default {
  name: "Goodslis",
  data() {
    return {
      banners:{}
      ,prodcut:{}
      ,category:{}
      ,baseurl:''
    };
  },
  created() {
    this.baseurl = this.axios.defaults.baseURL;
    this.get_data();
  },
  mounted() {
  },
  updated(){
    this.lunbo_fun();

  }
  ,methods: {
    go_goods(id){
      let that = this ;
      that.$router.push({path:'/goods',query:{id:id}})
    }
    ,get_data(){
      let that = this ;
      that.axios.post('site',{}).then(res=>{
        that.prodcut = res.data.data.prodcut;
        that.category = res.data.data.category;
        that.banners = res.data.data.banners;
        console.log(res)
      })
    }
    ,lunbo_fun() {
      var mySwiper = new Swiper(".swiper-container", {
        pagination: ".swiper-pagination",
        loop: true,
        autoplay: {
          stopOnLastSlide:true
        },
        speed: 500,
      });
    }
    ,go_banners(url){
      // window.open(url, '_blank', 'width=320,height=568,menubar=no,toolbar=no,status=no,scrollbars=yes')
      window.location.href=url; 

    }
    ,go_details(id){
      this.$router.push({path:'/details',query:{id:id}})
    }
  }
};


