<template>
  <div class="box">
    <div class="h5">
      <h5>合家安商城协议</h5>
    </div>
    <div class="cont" v-html="cont" >
      
    </div>
  </div>
</template>
<script>
export default {
  name: "Agreement",
  data() {
    return {
      cont:''
    };
  },
  created() {
     var that = this ;
    let data = { data :{ token:localStorage.getItem('token')}} ;
    that.axios.post('/xieyi',data).then(res=>{
      that.cont = res.data.data
    })
  },
  mounted() {},
  methods: {}
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  .h5 {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    h5 {
      width: 100%;
      margin: 0 auto;
      max-width: 750px; /*no*/
      text-align: center;
      font-size: 20px;
      font-weight: 700;
      color: #fff;
      height: 40px;
      line-height: 40px;
      z-index: 999;
      background: url("../../../static/images/bg.png") no-repeat;
    }
  }
  .cont {
    width: 100%;
    margin: 0 auto;
    padding-top: 40px;
  }
}
</style>



