<template>
  <div class="box">
    <div class="top">
      <p> <span @click="$router.go(-1)" class="el-icon-back"></span> 商品详情</p>
    </div>
    <!-- 轮播图 -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(items,index) in data_lis.pics" :key="index"  >
          <img class="ban_img" :src="baseurl+items" alt >
        </div>
      </div>
    </div>
    <div class="cont_box">
      <p>产品名称:&nbsp;{{data_lis.name}}</p>
      <p>
        价格:
        <i>￥{{data_lis.sale_const}}</i>
        <span>保险费:￥{{data_lis.retail_price}}</span>
      </p>
      <div class="title">商品详情</div>
    </div>
    <div class="fuwenben" v-html="data_lis.content"></div>
    <div class='operation' v-if="show">
      <div class="operation_box">
        <span  @click="isshow"  class="el-icon-error icon" style="height:100%"></span>
        <img class="img" :src="img_url" alt="">
        <div class="name">
          <p>{{data_lis.name}}</p>
          <p>库存:{{goods_data.stock}}</p><p>价格:￥{{goods_data.sale_const}} &nbsp;&nbsp; 保险费:￥{{data_lis.retail_price}}&nbsp;&nbsp;</p>
          </div>
        <div class="overflow"  style="width:96%;margin:0 auto">
          <!-- 颜色 -->
          <el-radio-group v-if="data_lis.product_pic.length!=0" v-model="goods_type.pic_id"  @change='chang_goods_type(2)'  class="color clearfix " size="mini">
            <el-radio-button style="float:left;margin:8px 0px 0 0;" :label="items.id"  v-for="(items,index) in data_lis.product_pic" :key="index" ><img  :src="baseurl + items.pic" alt="">{{items.pic_name}}</el-radio-button>
          </el-radio-group>
          <!-- 尺寸 -->
          <div class="chicun" v-if="data_lis.product_parameters.length!=0" >
            <div class="span"   >{{data_lis.product_parameters.name}} </div>
              <el-radio-group   @change='chang_goods_type' v-model="goods_type.parameter_id.parameter_value"    class="clearfix " style="vertical-align: middle;display: inline-block;" size="mini">
                <el-radio-button v-for="(ites,i) in data_lis.product_parameters.values" style="margin:3px 0;" :label="ites"   :key="i" ></el-radio-button>
              </el-radio-group>
          </div>
          <!-- 属性 -->
          <div class="shuxing" v-if="data_lis.product_plus_price.length!=0">
            <span class="span">属性</span>
            <el-radio-group v-model="goods_type.plus_price_id"  @change='chang_goods_type(3)'  class="color1 clearfix " size="mini">
              <el-radio-button style="float:left;margin:8px 0px 0 0;" :label="items.id"  v-for="(items,index) in data_lis.product_plus_price" :key="index" >{{items.name}}</el-radio-button>
            </el-radio-group>
          </div>
           <div v-if='data_lis.product_pic.length ==0 ' class="zhanwei"></div>
          <div class="num" >
            <span>数量</span>
            <el-input-number
          v-model="number"
          @change="handleChange"
          :min="0"
          :max="goods_data.stock"
          label="描述文字"
          size="small"
          style="margin-left:18px;"
        ></el-input-number>
          </div>
        </div>
            <el-button @click="add_cart" style="display:block;margin:38px auto 0;width:258px" type="danger">确认</el-button>
      </div>
    </div>
    <div class="foots" v-if="!show">
      <ul class="clearfix">
        <li>
          <el-button @click="isshow" type="success" size="mini" >加入提货</el-button>
        </li>
        <li>
          <el-button @click="isshow" type="warning" size="mini" >立即提货</el-button>
        </li>
        <li>
         
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import details from "./details";
export default details;
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  padding-top: 37px;
  
  background: #f3f0f0;
  .ban_img{
    width: 100%;
    max-height:188px ;
  }
  .operation{
    position: fixed;
    left: 0;
    width: 100%;
    bottom: 0;
    z-index: 100000;
    .operation_box{
      width: 100%;
      max-width: 750px;/*no*/
      margin: 0 auto;
      background: #fff;
      height: 358px;
      position: relative;
      border-top:1px solid #f53131;/*no*/
      .icon{
        position: absolute;
        top: 6px;
        right: 6px;
        color: #f53131;
        font-size: 28px;
      }
      .overflow{
        overflow: auto;
        height: 265px;
        .color{
            margin: 58px  0 0;
            width: 96%;
            padding-bottom:6px ;
            border-bottom:1px solid #ccc;/*no*/
            img{
              display: inline-block;
              max-width: 48px;
              max-height: 26px;
              margin-right:6px; 
              vertical-align: middle;
            }
        }
        .shuxing{
          padding-bottom:6px ;
            border-bottom:1px solid #ccc;/*no*/
          .span{
            min-width: 10%;;margin-left:12px;display: inline-block;
          }
          .color1{
            margin: 0 auto;
            display: inline-block;
          }

        }
        .chicun{
          border-bottom:1px solid #ccc; /*no*/
          .el-radio-button__inner{
            border: 1px solid #dcdfe6!important;/*no*/
          }
          .span{
            min-width: 10%;
            margin: 12px 0 0 12px ;
            display: inline-block;
            
          }
        }
        .zhanwei{
          width: 100%;
          height: 58px;
        }
        .num{
          margin:28px 0 18px;
          span{
            min-width: 10%;;margin-left:12px;display: inline-block;
          }
        }
      
      }
      .img{
        position: absolute;
        top: -44px;
        left: 8px;
        width: 88px;
        height: 88px;
        border: 1px solid #ccc;/*no*/
        background: #fff;
        z-index: 999;
      }
      .name{
        position: absolute;
        top: 8px;
        left: 106px;
        font-size: 12px;
        width: 60%;
        p:nth-child(2){
        margin: 6px 0 ;
        }
        p:last-child{
          color: #f53131;
          
        }
      }
    }
  }
  .top {
    position: fixed;
    top: 0;
    text-align: center;
    height: 38px;
    line-height: 38px;
    width: 100%;
    font-size: 14px;
    p {
      color: #fff;
      background: url("../../../static/images/bg.png") no-repeat;
      width: 100%;
      max-width: 750px;/*no*/
      position: relative;
      span{
        position: absolute;
        top: 0;
        left: 12px;
        height: 38px;
        line-height: 38px;
        font-size: 18px;
        font-weight: 700;
      }
    }
  }
  .cont_box {
    margin: 12px auto 0;
    width: 90%;
    font-size: 14px;
    p {
      word-break: break-all;
      margin-top: 12px;
      span {
        float: right;
        margin-right: 28px;
        color: #f53131;
      }
      i {
        color: #f53131;
      }
    }
    .title {
      margin-top: 12px;
      font-size: 16px;
      font-weight: 600;
    }
  }
  .fuwenben {
    margin: 12px auto 0;
    width: 90%;
  }
  .foots {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 10000;
    height: 58px;
    line-height: 58px;

    ul {
      max-width: 750px; /*no*/
      width: 100%;
      margin: 0 auto;
      background: #fff;
      li {
        float: left;
        width: 50%;
        text-align: center;
      }
    }
  }
}
</style>



