<template>
  <div class="box">
    <h5>
      <span @click="$router.go(-1)" class="el-icon-back"></span>换货办理
    </h5>
    <div class="city">
      <div class="xiangxi">
        <span>订单号&nbsp;:</span>
        <el-input
          v-model="data_lis.order_sn"
          placeholder="请前往提货明细选择"
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
          :disabled="true"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>产品名称&nbsp;:</span>
        <el-input
          v-model="data_lis.productname"
          placeholder="请前往提货明细选择"
          size="mini"
          style="display:inline-block;width:75%"
          :disabled="true"
        ></el-input>
        <p>ps:订单号和商品名称请前往提货明细选择换货操作</p>
      </div>
      <div class="xiangxi" v-if="data_lis.created_at">
        <span style="margin:4px 0;">商品图片&nbsp;:</span>
        <img :src="baseurl + data_lis.pic_url" alt>
      </div>
      <div class="xiangxi" v-if="data_lis.created_at">
        <span style="margin:4px 0;">购买时间&nbsp;:</span>
        {{ data_lis.created_at | time}}
      </div>
      <div class="xiangxi">
        <span>快递单号&nbsp;:</span>
        <el-input
          v-model="order_sn"
          placeholder="请输入您换货邮寄的快递单号"
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
          clearable
        ></el-input>
        <p>ps:请使用指定快递<i style="color:red;display:inline-block;margin:0 3px">{{kuaidi}}</i>;细节请于客服联系;</p>
      </div>
      <div class="xiangxi">
        <span>换货原因&nbsp;:</span>
        <el-input
          v-model="details"
          placeholder="请输入换货原因"
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
          clearable
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>收货人&nbsp;:</span>
        <el-input
          v-model="real_name"
          placeholder="请输入联系人姓名"
          size="mini"
          style="display:inline-block;width:75%"
          clearable
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>联系电话&nbsp;:</span>
        <el-input
          v-model="mobile"
          placeholder="请输入联系人电话"
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
          clearable
        ></el-input>
      </div>
      <div class="xiangxi" style="margin:12px 0">收货地址&nbsp;:</div>
      <citycom
        :province_ids="updata_data.province"
        :city_ids="updata_data.city"
        :district_ids="updata_data.district"
        v-on:province_id="province_ids"
        v-on:city_id="city_ids"
        v-on:district_id="district_ids"
      ></citycom>
      <div class="xiangxi">
        <span>详细地址&nbsp;:</span>
        <el-input
          v-model="detailed"
          placeholder="请输入详细地址"
          size="mini"
          style="display:inline-block;width:75%"
          clearable
        ></el-input>
        <p>ps:如有问题请联系客服,联系方式在售后页面;</p>
      </div>
    </div>
    <el-button
      @click="my_btn"
      type="warning"
      style="display:block;width:80%;margin:28px auto;"
    >提交换货申请</el-button>
  </div>
</template>


<script>
import citycom from "@/components/city/city";
export default {
  name: "Huanhuobanli",
  data() {
    return {
      data_lis: {},
      details: null,
      order_sn: null,
      kuaidi: null,
      real_name: null,
      mobile: null,
      province: null,
      city: null,
      district: null,
      detailed: null,
      updata_data: {
        province: null,
        city: null,
        district: null
      },
      baseurl: ""
    };
  },
  components: {
    citycom
  },
  created() {
    var that = this;
    this.baseurl = this.axios.defaults.baseURL;
    that.get_dizhi();
    if (that.$route.query.id) {
      let data = { data: { token: localStorage.getItem("token") } };
      that.axios
        .post("user/user_huanhuo/create/" + that.$route.query.id, data)
        .then(res => {
          console.log(res.data);
          that.data_lis = res.data.data;
          that.kuaidi = that.data_lis.express;
        });
    }
  },
  mounted() {},
  methods: {
    updata_city() {
      var that = this;
      let data = { data: { token: localStorage.getItem("token") } };
      data.data.real_name = that.real_name;
      data.data.mobile = that.mobile;
      data.data.province = that.province;
      data.data.district = that.district;
      data.data.city = that.city;
      data.data.detailed = that.detailed;
      that.axios.post("user/user_address/create", data).then(res => {
        if (res.data.status) {
        } else {
          that.$message.error(res.data.message);
        }
      });
    },
    my_btn() {
      var that = this;
      if (that.$route.query.id) {
        that.updata_city();
        if(that.order_sn==null || that.order_sn == ''){
          that.$message.error('请输入快递单号');
          return ;
        }
        let data = { data: { token: localStorage.getItem("token") } };
        data.data.real_name = that.real_name;
        data.data.mobile = that.mobile;
        data.data.province = that.province;
        data.data.district = that.district;
        data.data.city = that.city;
        data.data.detailed = that.detailed;
        data.data.order_sn = that.order_sn;
        data.data.details = that.details;
        that.axios
          .post("user/user_huanhuo/create/" + that.$route.query.id, data)
          .then(res => {
            console.log(res.data);
            if (res.data.status) {
              that.$message({
                message: res.data.message,
                type: "success"
              });
              that.$router.push({ path: "/home/users" });
            } else {
              that.$message.error(res.data.message);
            }
          });
      } else {
        that.$message.error("请您选择换货商品");
      }
    },
    get_dizhi() {
      var that = this;
      that.axios
        .post("user/user_address/create", {
          data: { token: localStorage.getItem("token") }
        })
        .then(res => {
          that.real_name = res.data.data.real_name == 0 ? null : res.data.data.real_name;
          that.mobile = res.data.data.mobile == 0 ? null : res.data.data.mobile ;
          that.province = res.data.data.province;
          that.city = res.data.data.city;
          that.district = res.data.data.district;
          that.detailed = res.data.data.detailed == 0 ? null : res.data.data.detailed  ;
          that.updata_data.province = res.data.data.province;
          that.updata_data.city = res.data.data.city;
          that.updata_data.district = res.data.data.district;
        });
    },
    //城市三级联动
    province_id(value) {
      this.province = value;
    },
    city_id(value) {
      this.city = value;
    },
    district_id(value) {
      this.district = value;
    },
    //修改的三级联动
    province_ids(value) {
      this.updata_data.province = value;
      this.province = value;
    },
    city_ids(value) {
      this.updata_data.city = value;
      this.city = value;
    },
    district_ids(value) {
      this.updata_data.district = value;
      this.district = value;
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
    position: relative;
    span {
      position: absolute;
      top: 0;
      left: 12px;
      height: 38px;
      line-height: 38px;
      font-size: 18px;
      font-weight: 700;
    }
  }
  .city {
    width: 92%;
    margin: 48px auto;
    font-size: 14px;
    text-align: left;
    .xiangxi {
      margin: 6px auto;
      p {
        width: 96%;
        margin: 6px auto 0;
        color: #ccc;
        font-size: 12px;
      }
      span {
        display: inline-block;
        width: 22%;
      }
      img {
        display: inline-block;
        width: 60%;
        vertical-align: middle;
      }
    }
  }
}
</style>



