<template>
  <div class="box">
    <h5>
      <span @click="$router.go(-1)" class="el-icon-back"></span>提货记录
    </h5>
    <el-table :data="tableData" style="width: 98%;margin:12px auto 0;"  >
      <el-table-column label="日期" :width="tableWidth" fixed>
        <template slot-scope="scope">{{scope.row.created_at | time}}</template>
      </el-table-column>
      <el-table-column prop="order_sn" label="订单号"  :width="tableWidth" ></el-table-column>
      <el-table-column prop="productname" label="产品名称" :width="tableWidth" ></el-table-column>
      <el-table-column prop="sale_const" label="单价" :width="tableWidth" ></el-table-column>
      <el-table-column prop="retail_price" label="保险费"  :width="tableWidth"></el-table-column>
      <el-table-column label="数量" >
        <template slot-scope="scope">1</template>
      </el-table-column>
      <el-table-column label="实付金额"  :width="tableWidth">
        <template slot-scope="scope">
          <span v-if="!scope.row.kalman">{{scope.row.retail_price*1 + scope.row.sale_const*1}}</span>
          <span v-if="scope.row.kalman">{{scope.row.retail_price}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="type" label="支付方式" :width="tableWidth" ></el-table-column>
      <el-table-column prop="status" label="货品状态" :width="tableWidth" ></el-table-column>
      <el-table-column prop="express_id" label="快递单号" :width="tableWidth" ></el-table-column>
      <el-table-column  label="收货地址" :width="tableWidth" >
        <template slot-scope="scope">
          {{scope.row.orders.detailed}}
        </template>
      </el-table-column>
      <el-table-column label="操作" :width="tableWidth">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status=='已完成'" @click="tihuo(scope.row.id)" type="text">申请换货</el-button>
          <el-button @click="del_data(scope.row.id)" type="text">删除订单</el-button>
          <el-button  v-if="scope.row.status=='已发货'" @click="is_ok(scope.row.id)" type="text">确认收货</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page" >
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
    </div>
  </div>
</template>
<script>
import pagecon from '@/components/page/pagecon'

export default {
  name: "Tihuolis",
  data() {
    return {
      num: null,
      pages: null,
      tableWidth: window.innerWidth * 0.45,
      tableData: []
    };
  },
  
  created() {
    this.get_lis();
  },
    components: {
    pagecon
  }
 , mounted() {},
  methods: {
    get_lis(e) {
      var that = this;
      let data = { data: { token: localStorage.getItem("token") } ,page:e};
      that.axios.post("user/user_buy", data).then(res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      });
    },
    is_ok(id){
      var that = this ;
      let data = {data:{token:localStorage.getItem('token'),id,status:4}};
      that.axios.post("user/user_buy/user_buy_setting", data).then(res => {
        if(res.data.status){
          that.get_lis();
        }else{that.$message(res.data.message)}
      });
    }
    ,tihuo(id) {
      var that = this;
      that.$router.push({ path: "/home/huanhuobanli", query: { id } });
    },
    del_data(id) {
      var that = this;
      let data = { data: { token: localStorage.getItem("token"), id } };
      that.axios.post("/user/user_buy/destroy", data).then(res => {
        console.log(res);
        if (res.data.status) {
          that.$message({
            message: res.data.message,
            type: "success"
          });
          that.get_lis();
        } else {
          that.$message.error(res.data.message);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  .page{
    width: 90%;
    margin: 12px  auto ;
  }
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
    position: relative;
    span {
      position: absolute;
      top: 0;
      left: 12px;
      height: 38px;
      line-height: 38px;
      font-size: 18px;
      font-weight: 700;
    }
  }
}
</style>



