<template>
  <div class="box">
    <div v-html="cont" class="top"></div>
    <div class="tel_box">
      <a href="javascript:;">
        <img src="../../../static/images/weixin.png" alt>
        <span>客服微信:{{weixin}}</span>
      </a>
      <a :href="'tel:'+ tel">
        <img src="../../../static/images/tel.png" alt>
        <span>客服电话:{{tel}}</span>
      </a>
    </div>
    <a class="dingwei" :href="'tel:'+ tel">
      <img src="../../../static/images/tel_gif.gif" alt>
    </a>
  </div>
</template>


<script>
import citycom from "@/components/city/city";
export default {
  name: "Shouhuo",
  data() {
    return {
      cont: null, //存放top内容
      tel: "18033623739",
      weixin: "125412232"
    };
  },

  created() {
    var that = this ;
    let data = { data :{ token:localStorage.getItem('token')}} ;
    that.axios.post('/after_sale',data).then(res=>{
      console.log(res.data)
      that.cont = res.data.data.kefu
      that.tel = res.data.data.mobile
      that.weixin = res.data.data.weixin
    })
  
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;

  // background: #f3f0f0;
  .top {
    width: 100%;
    margin-bottom: 12px;
  }
  .dingwei {
    position: fixed;
    right: 18px;
    bottom: 68px;

    img {
      display: block;
      width: 48px;
      height: 48px;
    }
  }
  .tel_box {
    width: 90%;
    margin: 0 auto;
    a {
      width: 100%;
      height: 100%;
      display: block;
      font-size: 16px;
      margin-top: 12px;
      img {
        display: inline-block;
        width: 28px;
        height: 28px;
        margin: 0 12px;
      }
    }
  }
}
</style>



