
export default {
  name: "Users",
  data() {
    return {
      img_url:''
      ,user_data:{}
      ,show:false
    };
  },
  created() {
    this.get_lis();
  },
  mounted() {
   
  },
  methods: {
    get_lis(){
      var that = this ;
      let data = {data:{token:localStorage.getItem('token')}};
          that.axios.post('/user/distribution',data).then(res=>{
              that.user_data = res.data.data
          })
    }
    ,get_evm(){
      var that = this ;
      let data = {data:{token:localStorage.getItem('token')}};
          that.axios.post('/user/user_qrcode',data).then(res=>{
              if(res.data.status){
                that.img_url = that.axios.defaults.baseURL + res.data.url ;
              }else{
                that.$message('对不起,二维码生成失败');
                that.show = false;
              }
          })
    }
    ,off(){
      this.show = !this.show;
      if(this.show){
        this.get_evm();
      }
    }
    ,del_token(){
      var that = this ;
      localStorage.removeItem('token');
      that.$router.push({path:'/login'});
    }
  }
};


