<template>
  <div class="box">
    <h5>
      <span @click="$router.go(-1)" class="el-icon-back"></span>换货记录
    </h5>
    <el-table :data="tableData" style="width: 98%;margin:12px auto 0;" >
      <el-table-column label="日期" :width="tableWidth" fixed>
        <template slot-scope="scope">{{scope.row.updated_at | time}}</template>
      </el-table-column>
      <el-table-column label="订单号" :width="tableWidth">
        <template slot-scope="scope">{{scope.row.user_buy.order_sn}}</template>
      </el-table-column>
      <el-table-column label="产品名称" :width="tableWidth">
        <template slot-scope="scope">{{scope.row.user_buy.productname}}</template>
      </el-table-column>
      <el-table-column prop="status" label="处理状态" :width="tableWidth"></el-table-column>
      <el-table-column prop="order_sn" label="您的快递单号" :width="tableWidth"></el-table-column>
      <el-table-column prop="express_id" label="二次发货快递单号" :width="tableWidth"></el-table-column>
      <el-table-column prop="detailed" label="收货地址" :width="tableWidth"></el-table-column>
      <el-table-column label="操作" :width="tableWidth">
        <template slot-scope="scope">
          <el-button @click="del_data(scope.row.id)" type="text">取消换货/删除</el-button>
        </template>
      </el-table-column>
    </el-table>
     <div class="page" >
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
    </div>
  </div>
</template>
<script>
import pagecon from '@/components/page/pagecon'

export default {
  name: "Huanhuolis",
  data() {
    return {
      num: null,
      tableWidth: window.innerWidth * 0.45,
       pages: null,
      tableData: []
    };
  },
  created() {
    this.get_lis();
  },
   components: {
    pagecon
  }
  ,mounted() {},
  methods: {
    get_lis(e) {
      var that = this;
      let data = { data: { token: localStorage.getItem("token") },page:e };
      that.axios.post("/user/user_huanhuo", data).then(res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      });
    },
    del_data(id) {
      var that = this;
      let data = {data:{token:localStorage.getItem('token'),id}}
      that.axios.post("/user/user_huanhuo/destroy", data).then(res => {
        console.log(res.data);
        if (res.data.status) {
          that.$message({
            message: res.data.message,
            type: "success"
          });
          that.get_lis();
        } else {
          that.$message.error(res.data.message);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  .page{
    width: 90%;
    margin: 12px  auto ;
  }
  h5 {
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    height: 40px;
    line-height: 40px;
    background: url("../../../static/images/bg.png") no-repeat;
    position: relative;
    span {
      position: absolute;
      top: 0;
      left: 12px;
      height: 38px;
      line-height: 38px;
      font-size: 18px;
      font-weight: 700;
    }
  }
}
</style>



